<template>
  <div class="row">
    <div class="title">
      当前项目
    </div>

    <div class="confVal">
      <!-- eslint-disable -->
        <a-select
          v-model:value="curProjectCode"
          class="config_projectCode_select"
          @change="event$change('projectCode')"
        >
          <a-select-option
            v-for="item in projectCodeDict"
            :key="item.identification"
            :value="item.identification"
          >
            {{ item.name }}
          </a-select-option>
        </a-select>
        
        <a-button class="config_projectCode_get" type="primary" @click="matchProjectCode">
          识别
          <TooltipIcon>
            自动识别当前项目, 当前程序对不同的项目开发了定制功能
          </TooltipIcon>
        </a-button>
    </div>
  </div>
  <div class="row projectInfo" v-if="store$gitConfig && curProjectCode">
    <div class="content">
      <div class="contentItem">
        <span class="title">当前分支:</span>
        <span class="value">{{ store$gitConfig.branch }}</span>
      </div>
      <div class="contentItem">
        <span class="title">项目地址:</span>
        <span class="value">{{ store$gitConfig.remoteList[0].remoteUrl }}</span>
      </div>
      
    </div>
  </div>
</template>

<script setup>
// 在 script setup 中, 所有定义的变量都可以被 template 识别
import { ref, computed, watch } from 'vue'
import TooltipIcon from '@/common/tooltipIcon.vue'
import { httpServer } from '@/apis'
import { useStore } from 'vuex'

// store依赖数据查询
const store = useStore()

// computed
const store$curProjectId = computed(() => store.state.curProjectId)
const store$vscodeState = computed(() => store.state.vscodeState)
const store$gitConfig = computed(() => store$vscodeState.value?.gitConfig)
const store$geekExpandConfig = computed(() => store$vscodeState.value?.geekExpandConfig)

// DICT
const projectCodeDict = computed(() => store.state.projectCodeDict)
const curGitProjectName = computed(() => {
  if (store$gitConfig.value) {
    const remoteUrl = store$gitConfig.value.remoteList[0].remoteUrl
    const findItem = projectCodeDict.value.find((item) => {
      const { http, ssh } = item.git
      return ssh === remoteUrl || http === remoteUrl
    })

    if (findItem) {
      return findItem.identification
    }
  }
  return ''
})

// 定义data数据(先查sotre, 没有则取geekExpandConfig)
const curProjectCode = ref(store$curProjectId.value || store$geekExpandConfig.value?.projectId || '')
watch(store$curProjectId, (value) => { curProjectCode.value = value })
const event$change = (type) => {
  switch (type) {
    case 'projectCode':
      httpServer('changeProjectId', curProjectCode.value, () => {
        store.dispatch('setProjectId', curProjectCode.value)
      })
      break
  }
}

// event
const matchProjectCode = () => {
  curProjectCode.value = curGitProjectName.value
  event$change('projectCode')
}
</script>

<style scoped lang="scss">
  @import "./main.scss";

  .config_projectCode_select {
    width: 150px;
  }

  .config_projectCode_get {
    width: 70px;
    font-size: 12px;
    margin-left: 15px;
    border-radius: 0px;
  }

  .projectInfo {
    .content {
      padding: 5px;
      width: 100%;
      max-width: 500px;
      margin-left: 100px;
      border: 1px solid #eee;
      background: #000;
      color: #fff;
      font-size: 12px;

      .contentItem {
        display: flex;
        flex-direction: row;
        margin-bottom: 2px;

        .title {
          width: 80px;
          line-height: 18px;
        }

        .value {
          flex: 1;
        }
      }

      .remoteInput {
        margin-bottom: 5px;
      }
    }
  }
</style>
