<template>
  <div class="row">
    <div class="title">
      拉取接口
    </div>

    <div class="confVal">
      <!-- 自动 -->
      <!-- eslint-disable -->
      <a-radio-group class="serviceType" v-model:value="curServiceType" @change="event$change('serviceType')">
        <a-radio-button value="auto">
          使用当前项目服务接口
        </a-radio-button>
        <a-radio-button value="custom">
          使用自定义服务接口
        </a-radio-button>
      </a-radio-group>

      <!-- 接口地址 -->
      <a-input-group v-if="curServiceType === 'custom'" class="serviceApiUrl" compact>
        <a-select class="serviceApiType" v-model:value="curRequestType" @change="event$change('requestType')">
          <a-select-option
            v-for="typeItem in requestTypeDict"
            :key="typeItem.value"
            :value="typeItem.value"
          >
            {{ typeItem.label }}
          </a-select-option>
        </a-select>
        <a-input class="serviceUrl" v-model:value="curRequestUrl" placeholder="http://TODO:IP/APIURL  (目前仅支持GET接口)" @change="event$change('requestUrl')" />
      </a-input-group>
      <div v-else>
        <!-- TODO: -->
      </div>
    </div>
  </div>
</template>

<script setup>
import { useStore } from 'vuex'
import { computed, ref, watch } from 'vue'
import { httpServer } from '@/apis'

// store依赖数据查询
const store = useStore()

const requestTypeDict = computed(() => store.state.requestTypeDict)
const store$geekExpandConfig = computed(() => store.state.vscodeState.geekExpandConfig)
const service$type = computed(() => store$geekExpandConfig.value?.serviceType)
const service$requestType = computed(() => store$geekExpandConfig.value?.requestType)
const service$requestUrl = computed(() => store$geekExpandConfig.value?.requestUrl)

// 回填服务器数据
const curServiceType = ref(service$type.value || 'auto')
const curRequestUrl = ref(service$requestUrl.value || '')
const curRequestType = ref(service$requestType.value || requestTypeDict.value[0].value)

// 更新服务器/自动回填
watch(service$type, (value) => { curServiceType.value = value })
watch(service$requestUrl, (value) => { curRequestUrl.value = value })
watch(service$requestType, (value) => { curRequestType.value = value })

// change发送至服务端
const event$change = (type) => {
  switch (type) {
    case 'serviceType':
      httpServer('setServiceType', curServiceType.value)
      break
    case 'requestType':
      httpServer('setRequestType', curRequestType.value)
      break
    case 'requestUrl':
      httpServer('setRequestUrl', curRequestUrl.value)
      break
    default:
      break
  }
}
</script>

<style scoped lang="scss">
  @import "./main.scss";

  .serviceType {
    margin-bottom: 10px;
  }

  .serviceApiUrl {
    width: 500px;
    margin-bottom: 10px;
    .serviceApiType {
      width: 90px;
    }

    .serviceUrl {
      width: 410px;
    }
  }

  .msg {
    max-width: 500px;
  }
</style>
