<template>
  <div class="row">
    <div class="title">
      更新模式
    </div>

    <div class="confVal">
      <!-- eslint-disable -->
      <a-select
        v-model:value="autoPattern"
        class="config_i18nAuto_select"
        @change="event$change('autoPattern')"
      >
        <a-select-option
          v-for="item in i18nAutoDict"
          :key="item.value"
          :value="item.value"
        >
          {{ item.label }}
        </a-select-option>
      </a-select>

      <div class="tooltip">
        {{ tooltipText }}
      </div>
    </div>
  </div>

  <div class="row">
    <div class="title">
      检查规则
    </div>

    <div class="confVal">
      <a-input-group compact class="customCheck">
        <a-select v-model:value="matchI18nType" @change="event$change('matchI18nType')">
          <a-select-option value="def">
            常规
          </a-select-option>
          <a-select-option value="regexp">
            正则
          </a-select-option>
        </a-select>
        <a-input class="customCheckInput" v-model:value="matchI18nRegexp" @change="event$change('matchI18nRegexp')"/>
      </a-input-group>
      
      <div class="regexpContent">
        最终匹配正则：<span class="regText">{{ curRegexpText }}</span>
      </div>
    </div>
  </div>

  <!-- i18n服务设置 (自动获取, 自动获取需要先设置当前项目 or 自定义配置) -->
  <!--  -->
</template>

<script setup>
import { computed, ref, watch } from 'vue'
import { httpServer } from '@/apis'
import { useStore } from 'vuex'

// store依赖数据查询
const store = useStore()
const store$geekExpandConfig = computed(() => store.state.vscodeState.geekExpandConfig)
const service$autoPattern = computed(() => store$geekExpandConfig.value?.autoPattern)
const service$matchI18nType = computed(() => store$geekExpandConfig.value?.matchI18nType)
const service$matchI18nRegexp = computed(() => store$geekExpandConfig.value?.matchI18nRegexp)

// DICT
const i18nAutoDict = computed(() => store.state.i18nAutoDict)

// 默认回填
const autoPattern = ref(service$autoPattern.value || 'none')
const matchI18nType = ref(service$matchI18nType.value || 'def')
const matchI18nRegexp = ref(service$matchI18nRegexp.value || 't')

// 级联提示
const tooltipText = computed(() => i18nAutoDict.value.find(item => item.value === autoPattern.value)?.remarks || '')
const curRegexpText = computed(() => {
  if (matchI18nType.value === 'def') {
    return `/(?:${matchI18nRegexp.value.split(',').join('|')}) {0,}\( {0,}['"]([^'"]+)['"] {0,}\)/g`
  }
  return matchI18nRegexp.value
})

// service变更回填
watch(service$autoPattern, (value) => { autoPattern.value = value })
watch(service$matchI18nType, (value) => { matchI18nType.value = value })
watch(service$matchI18nRegexp, (value) => { matchI18nRegexp.value = value })

// change发送至服务端
const event$change = (type) => {
  switch (type) {
    case 'autoPattern':
      httpServer('setAutoPattern', autoPattern.value)
      break
    case 'matchI18nType':
      httpServer('setMatchI18nType', matchI18nType.value)
      break
    case 'matchI18nRegexp':
      httpServer('setMatchI18nRegexp', matchI18nRegexp.value)
      break
    default:
      break
  }
}
</script>

<style scoped lang="scss">
  @import "./main.scss";
  .config_i18nAuto_select {
    width: 225px;
  }

  .customCheck {
    width: 225px;
    display: flex !important;
    flex-direction: row;

    .customCheckInput {
      flex: 1;
    }
  }

  .tooltip, .regexpContent {
    color: #bbb;
    font-size: 12px;
  }

  .regText {
    color: #FFF;
    font-weight: 900;
    letter-spacing: 1.2px;
  }
</style>
